<template>
    <div>
        <van-nav-bar style="position:fixed;top:0;z-index: 10; box-shadow: 0px -5px 10px #888888;width: 100%;" :title="blog.title"
            left-text="返回" right-text="分享" @click-left="$router.go(-1)" left-arrow>
            <van-icon slot="right" style="transform: rotateZ(90deg)" name="ellipsis" @click="showPop" />
        </van-nav-bar>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" style="padding: 60px 10px 100px;">
            <div style="font-size: 0.9rem;line-height: 1.5;color: #303133;border-bottom: 1px solid #E4E7ED;padding: 0px 10px">
                <span style="font-family: '微软雅黑'; color: #303133;">{{blog.description}}</span>
            </div>
            <div v-html="blog.content" class="markdown-body" style="padding: 10px"></div>
            <div style="font-size: 0.9rem;line-height: 1.5;color: #606c71; text-align: right;"><span style="padding-right: 10px;"> {{blogDescribe}} </span></div>
            <div style="font-size: 0.9rem;line-height: 1.5;color: #606c71; text-align: right;"><span style="padding-right: 10px;"> {{blog.updateTime}} </span></div>
        </van-pull-refresh>
        <div @click="hidePop" style="position:fixed;top:0; right:0; z-index: 2000; width: 100%; height: 100%; background: rgba(0,0,0,0.5)" v-show="ispop"></div>
        <transition name="fade">
            <div class="pop" @click="hidePop" v-show="ispop">
                <div>2222</div>
                <div>3</div>
            </div>
        </transition>
    </div>
</template>


<script>
    import { mapGetters } from 'vuex'
    import GistApi from '@/api/gist'
    export default {
        data() {
            return {
                ispop: false,
                isLoading: false,
                blog: {
                    id: "",
                    title: "",
                    content: "",
                    description: "",
                    createTime: "",
                    updateTime: "",
                    type: "",
                    tags: "",
                }
            }

        },
        computed: {
            ...mapGetters([
                'blogDescribe',
            ])
        },
        mounted() {
            this.$toast.loading({
                duration: 0,
                forbidClick: true,
                message: '加载中'
            })
            this.blog.id = this.$route.params.id
            GistApi.single(this.blog.id).then((response) => {
                let result = response.data
                for (let key in result.files) {
                    this.blog['title'] = key
                    this.blog['content'] = this.$markdown(result.files[key]['content'])
                    //this.blog['description'] = result['description']
                    let description = result['description']
                    this.blog['description'] = description.split('||')[1]
                    this.blog['type'] = description.split('||')[0].split('>')[0]
                    this.blog['tags'] = description.split('||')[0].split('>')[1].split('&')
                    this.blog['createTime'] = this.$util.utcToLocal(result['created_at'])
                    this.blog['updateTime'] = this.$util.utcToLocal(result['updated_at'])
                    break
                }
            }).then(() => this.$toast.clear())
        },
        methods: {
            onRefresh() {
                setTimeout(() => {
                    this.$toast('刷新成功');
                    this.isLoading = false;
                }, 500);
            },
            showPop() {
                this.ispop = !this.ispop
                console.log('show')
            },
            hidePop() {
                let that = this
                that.ispop = !that.ispop
                console.log('hide')
                //$('.pop').on('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function(){
                //    that.ispop = !that.ispop
                //    console.log(123)
                //    $('.pop').off()
                //})
            },
        }

    }
</script>

<style scoped lang="less">
    .pop {
        position:fixed;
        right:0;
        top: 0;
        z-index: 2001;
        background: #fff;
        border-radius:5px;
        width: 80%;
        margin: 0px 5px 0 0;
        &.fade-enter-active, &.fade-leave-active {
            transition: all 0.5s;
        }
        &.fade-enter {
            top: -100px;
        }
        &.fade-leave-to {
            opacity: 0;
        }
    }

</style>


